import useUser from "../../api/user";
import Layout from "../../components/Layout";
import QrCode from "../../components/QrCode";
import { useTitle } from "../../utils/useTtile";
import styles from "./styles.module.css"

export default function PromotePage({ role }: PromoteProps)
{
	useTitle("");
	const user = useUser();
	const roleText = {
		"worker": "",
		"manager": "",
	}[role];
	const roleText2 = {
		"worker": "",
		"manager": "",
	}[role];
	if (!roleText) return <Layout>!</Layout>;

	return (
		<Layout centered gap="1rem" height100 className={styles.root} homeBtn forStaff>
			<h1>  {roleText}</h1>
			<div className={styles.qr}>
				<QrCode code={`promote_${role}_${user.data?.id}`} colorBg="#ffffff00" scale={13} />
			</div>
			<h3>    {roleText2}</h3>
		</Layout>
	);
}

interface PromoteProps
{
	role: "worker" | "manager",
}
