import { Link } from "react-router-dom";
import Layout from "../../components/Layout";
import IconCancel from "../../icons/cancel";
import IconSave from "../../icons/save";
import styles from "./styles.module.css"
import useStateObj from "../../utils/useStateObj";
import { useMutationGameCounter, useMutationGameDuration, useMutationGameStartStr, useGameCounter, useGameDuration, useGameStartStr } from "../../api/game";
import Spinner from "../../components/Spinner";
import displayError from "../../utils/displayError";
import { useEffect } from "react";
import { useTitle } from "../../utils/useTtile";
import TourneyEdit from "./TourneyEdit";
import { useMutationTourneyEndGame, useMutationTourneyEndTourney, useMutationTourneyReset, useMutationTourneySelectNextGame, useMutationTourneyStartGame, useMutationTourneyUnendTourney, useTourneyData } from "../../api/tourney";
import ConfirmingButton from "../../components/ConfirmingButton";
import type { UseMutationResult, UseQueryResult } from "react-query";
import classNames from "../../utils/classNames";

export default function GameSettingsPage()
{
	useTitle(" ");
	const tourney = useTourneyData();

	return (
		<Layout centeredPage homeBtn gap="2rem" forStaff>
			<h1> </h1>
			{tourney.isLoading && <Spinner />}
			{displayError(tourney)}
			<Link to="/tourney_screen" className={styles.openScreen}> </Link>
			<Link to="/game_characters" className={styles.openScreen}> </Link>
			<Input text=" " type="text" query={useGameStartStr} getv={d => d?.startStr || ""} newv={inp => inp.value} mutation={useMutationGameStartStr} />
			<Input text="  (.)" type="number" query={useGameDuration} getv={d => d?.duration || 0} newv={inp => inp.valueAsNumber} mutation={useMutationGameDuration} />
			<Input text="  (.)" type="number" query={useGameCounter} getv={d => d?.counter || 0} newv={inp => inp.valueAsNumber} mutation={useMutationGameCounter} />
			<div className={styles.tourney}>
				<h3> </h3>
				<div>
					<ConfirmingButton className={styles.start} h="  ?" bt="" rt=" !" disabled={!tourney.data || tourney.data?.showGame} mutation={useMutationTourneySelectNextGame}>  </ConfirmingButton>
					<ConfirmingButton className={styles.start} h=" ?" bt="" rt=" !" disabled={!tourney.data || tourney.data?.showGame || tourney.data.curGameNodeId == -1} mutation={useMutationTourneyStartGame}> </ConfirmingButton>
					<ConfirmingButton className={styles.start} h=" ?" bt="" rt=" !" disabled={!tourney.data?.showGame} mutation={useMutationTourneyEndGame}> </ConfirmingButton>
				</div>
				<div>
					<div style={{ flexGrow: 1 }}></div>
					<ConfirmingButton className={classNames(styles.start, !tourney.data?.ended && styles.hidden)} h="  ?" bt="" rt="  !" mutation={useMutationTourneyUnendTourney}>  </ConfirmingButton>
					<ConfirmingButton className={classNames(styles.start, tourney.data?.ended && styles.hidden)} h=" ?" bt="" rt=" !" mutation={useMutationTourneyEndTourney}> </ConfirmingButton>
				</div>
			</div>
			<TourneyEdit />
			<ConfirmingButton className={styles.start} h="  ?" bt="" rt="  !" mutation={useMutationTourneyReset}>  </ConfirmingButton>
		</Layout>
	);
}

function Input<T, K extends number | string>({ text, type, getv, newv, query, mutation }: { text: string, type: "number" | "text", getv: (data?: T) => K, newv: (inp: HTMLInputElement) => K, query: () => UseQueryResult<T, unknown>, mutation: () => UseMutationResult<T, any, K, unknown> })
{
	const value = useStateObj(getv());
	const queryR = query();
	const mutate = mutation();

	// eslint-disable-next-line
	useEffect(() => value.set(getv(queryR.data)), [queryR.data]);

	return (
		<div className={styles.input}>
			{queryR.isLoading && <Spinner />}
			{mutate.isLoading && <Spinner />}
			{displayError(queryR)}
			{displayError(mutate)}
			<span>{text}</span>
			<input type={type} value={value.v} onChange={e => value.set(newv(e.target))} />
			{queryR.data && value.v != getv(queryR.data) && <>
				<button onClick={() => mutate.mutate(value.v)}><IconSave /></button>
				<button onClick={() => value.set(getv(queryR.data))}><IconCancel /></button>
			</>}
		</div>
	)
}
